export default {
  getNavList: (state) => {
    return state.navList
  },
  getServiceList: (state) => {
    return state.servicesList
  },
  getTableBarList: (state) => {
    return state.tableBarList
  },
  getTableTitleList: (state) => {
    return state.tableTitleList
  },
  getDocTableTitleList: (state) => {  //  医生列表
    return state.docTableTitleList
  }
}



